<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Password Reset Error" @close="handleError">
      <section v-html="error"></section>
    </base-modal>
    <base-modal :show="success" color="is-success" title="Password Reset Successful" @close="handleSuccess">
      Your password has been reset successfully. You can now log in using your new password.
    </base-modal>
    <div class="columns is-centered">
      <div class="column is-5-tablet is-5-desktop is-4-widescreen">
        <div class="box">
          <form @submit.prevent="doConfirmReset">
            <div class="field">
              <label class="label" for="username">Username</label>
              <div class="control has-icons-left">
                <input class="input" type="text" id="username" placeholder="Username" ref="userInput"
                       v-model.trim="username">
                <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="userIcon">&nbsp;</svg-icon>
                </span>
              </div>
            </div>
            <div class="field">
              <label class="label" for="newPassword">New Password</label>
              <div class="control has-icons-left">
                <input class="input" :class="{'is-danger': ko, 'is-success': ok}" type="password" id="newPassword"
                       ref="newPassword" v-model.trim="newPassword" @keyup="checkPassword">
                <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="passwordIcon">&nbsp;</svg-icon>
                </span>
              </div>
              <p class="help is-danger" v-if="ko">Passwords must match</p>
              <p class="help is-success" v-if="ok">Passwords do match</p>
            </div>
            <div class="field">
              <label class="label" for="confirmPassword">Confirm Password</label>
              <div class="control has-icons-left">
                <input class="input" :class="{'is-danger': ko, 'is-success': ok}" type="password" id="confirmPassword"
                       v-model.trim="confirmPassword" @keyup="checkPassword">
                <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="passwordIcon">&nbsp;</svg-icon>
                </span>
              </div>
              <p class="help is-danger" v-if="ko">Passwords must match</p>
              <p class="help is-success" v-if="ok">Passwords do match</p>
            </div>
            <article class="message is-danger" v-if="!formIsValid">
              <div class="message-body">
                Please make sure to enter the same password in both fields. Neither of them can be empty.
              </div>
            </article>
            <div class="field is-grouped">
              <div class="control">
                <button class="button is-link">Confirm Password Reset</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiLock} from '@mdi/js';
import {mdiAccount} from '@mdi/js';

export default {
  name: "PasswordSetForm",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  props: {
    token: {
      type: String,
      required: true
    }
  },
  computed: {
    userIcon() {
      return mdiAccount;
    },
    passwordIcon() {
      return mdiLock;
    }
  },
  data() {
    return {
      formIsValid: true,
      isLoading: false,
      success: false,
      error: null,
      username: '',
      newPassword: '',
      confirmPassword: '',
      ko: false,
      ok: false,
    };
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      const redirectUrl = '/' + (this.$route.query.goto || 'aircraft');
      this.$router.replace(redirectUrl);
    },
    checkPassword() {
      if (this.newPassword !== '' || this.confirmPassword !== '') {
        if (this.newPassword === this.confirmPassword) {
          this.ko = false;
          this.ok = true;
        } else {
          this.ko = true;
          this.ok = false;
        }
      } else {
        this.ko = false;
        this.ok = false;
      }
    },
    async doConfirmReset() {
      if (this.username === '' || this.newPassword === '' || this.confirmPassword === '' || this.newPassword !== this.confirmPassword) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        username: this.username,
        token: this.token,
        password: this.newPassword
      };
      try {
        await this.$store.dispatch('auth/passwordResetConfirm', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to complete password reset, try later.';
      }
      this.isLoading = false;
    }
  }
}
</script>

