<template>
  <section class="section">
    <div class="container">
      <password-set-form v-if="hasToken" :token="token"></password-set-form>
      <password-request-form v-else></password-request-form>
    </div>
  </section>
</template>

<script>
import PasswordRequestForm from "@/components/auth/PasswordRequestForm";
import PasswordSetForm from "@/components/auth/PasswordSetForm";
export default {
  name: "UserPassword",
  props: ['token'],
  components: {
    PasswordRequestForm,
    PasswordSetForm
  },
  computed: {
    hasToken() {
      return !!this.token;
    }
  }
}
</script>
