<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Password Reset Error" @close="handleError">
      <section v-html="error"></section>
    </base-modal>
    <base-modal :show="success" color="is-success" title="Password Reset Requested" @close="handleSuccess">
      A password reset operation has been requested for your account. Please check your emails for further instructions.
    </base-modal>
    <div class="columns is-centered">
      <div class="column is-5-tablet is-5-desktop is-4-widescreen">
        <div class="box">
          <form @submit.prevent="doRequestReset">
            <div class="field">
              <label class="label" for="username">Username</label>
              <div class="control has-icons-left">
                <input class="input" type="text" id="username" placeholder="Username" ref="userInput" v-model.trim="username">
                <span class="icon is-small is-left">
                    <svg-icon type="mdi" :path="userIcon">&nbsp;</svg-icon>
                  </span>
              </div>
            </div>
            <div class="field">
              <label class="label" for="email">Email Address</label>
              <div class="control has-icons-left">
                <input class="input" :class="{'is-danger': ko, 'is-success': ok}" type="email" id="email" placeholder="Email Address" v-model.trim="email" @keyup="checkMail">
                <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="emailIcon">&nbsp;</svg-icon>
                </span>
              </div>
              <p class="help is-danger" v-if="ko">Not a valid email address</p>
              <p class="help is-success" v-if="ok">Valid email address</p>
            </div>
            <article class="message is-danger" v-if="!formIsValid">
              <div class="message-body">
                Please make sure to enter a username and a valid email address. Neither of them can be empty.
              </div>
            </article>
            <div class="field is-grouped">
              <div class="control">
                <button class="button is-link">Request Password Reset</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiAccount} from '@mdi/js';
import {mdiAt} from '@mdi/js';

function emailIsValid (email) {
  return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
}

export default {
  name: "PasswordRequestForm",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  computed: {
    userIcon() {
      return mdiAccount;
    },
    emailIcon() {
      return mdiAt;
    }
  },
  data() {
    return {
      formIsValid: true,
      isLoading: false,
      success: false,
      error: null,
      username: '',
      email: '',
      ko: false,
      ok: false,
    };
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      const redirectUrl = '/' + (this.$route.query.goto || 'aircraft');
      this.$router.replace(redirectUrl);
    },
    checkMail() {
      if (this.email === '') {
        this.ok = false;
        this.ko = false;
        return;
      }
      if (emailIsValid(this.email)) {
        this.ok = true;
        this.ko = false;
      } else {
        this.ok = false;
        this.ko = true;
      }
    },
    async doRequestReset() {
      if (this.username === '' || this.email === '' || !emailIsValid(this.email)) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        username: this.username,
        email: this.email
      };
      try {
        await this.$store.dispatch('auth/passwordResetRequest', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to initiate password reset, try later.';
      }
      this.isLoading = false;
    }
  }
}
</script>
